import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class TableDataService {

  static  fromDate :any;
  static category :any;


  constructor() { }

  //setters
  setFromDate(iv:any) {  
    console.log("from set  date service "+iv);
    TableDataService.fromDate=iv;  
  }
  
  setCategory(iv:any) {  
    console.log("from set  category service "+iv);
    TableDataService.category=iv;  
  }

  //getters
  getFromDate()
  {
    console.log("from get date")
  return TableDataService.fromDate;
  }
  getCategory()
  {
    console.log("from get categry")
  return TableDataService.category;
  }
  

}
