import { Component, OnInit } from '@angular/core';
import { DatePipe } from '../../../../node_modules/@angular/common';
import * as moment from 'moment';
import { HttpClient } from '@angular/common/http';
import {NgForm} from '@angular/forms';
import { environment } from './../../../environments/environment';


@Component({
  selector: 'app-register-entry',
  templateUrl: './register-entry.component.html',
  styleUrls: ['./register-entry.component.css']
})
export class RegisterEntryComponent implements OnInit {
 

 
  // username:any;
  // password:any;
  d:any;
   caseNumber:any;
   reportedDate:any;
	 location:any;
	 description:any;
	 assignedOfficer:any;
	 entryBy:any;
  category:any;
  selectedValue:any;
  baseUrl = environment.baseUrl;
  entryselectedValue:any
 datePipe = new DatePipe('en-US');
constructor(private http: HttpClient) { }

  ngOnInit() {
  }
  submit(form:NgForm)
  {
    this.setformrecords().then((Response:any) =>
      {
         console.log("done submision!");
        //  this.caseNumber='';
        // this.reportedDate='';
        //  this.location=''
        // this.description='';
        // this. assignedOfficer='';
        // this. entryBy='';
        // this.category='';

        form.reset();
        alert("entry submitted !!");
       });




     console.log("inside method"+this.caseNumber+this.location+this.description+this.assignedOfficer+this.category+this.entryBy);
  }


  changeDataSource(data)
  {
    this.selectedValue=data;
    console.log("selected categry "+this.selectedValue);

  }
  changeentrySource(data)
  {
    this.entryselectedValue=data;
    console.log("entrysourse selected "+this.entryselectedValue);
  }


  public onDate(event): void {
    this.d = event.value;
    //this.reportedDate = this.datePipe.transform(this.d, 'dd/MM/yyyy');
    var formatted=moment(this.d).format('DD-MM-YYYY');
    console.log("reported date is "+formatted) ; 
  }


  setformrecords(){
    return new Promise((resolve, reject) => {         
      this.http.get(this.baseUrl+"blockchain/formData/"+this.caseNumber+'/'+this.location+'/'+this.description+'/'+this.assignedOfficer+'/'+this.entryselectedValue+'/'+this.selectedValue).subscribe((Response) =>{
          resolve(Response);
       },(error) => {
         reject(error);
       }
     );
     })}

}
