@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})

export class TableComponent implements OnInit {
  fromdate:any;
  category:any;
  ELEMENT_DATA:any;
  baseUrl = environment.baseUrl;

  displayedColumns: string[] = ['caseNumber', 'location', 'date', 'category','description','assignedOfficer','entryBy'];
  dataSource :any
  record: Record;




  constructor(private http: HttpClient,public _dataService: TableDataService,private route: ActivatedRoute) { 

    this.fromdate=this._dataService.getFromDate();
    this.category=this._dataService.getCategory();
    
    
   this.getitemrecords().then((Response:any) =>
   {
     
     // this.dataSource=Response;
       });




  }
  @ViewChild(MatSort) sort: MatSort;
  ngOnInit() 
  {


    this.route.paramMap.subscribe(params => {
      
      this.fromdate=this._dataService.getFromDate();
      this.category=this._dataService.getCategory();
      
      
     this.getitemrecords().then((Response:any) =>
     {
        this.dataSource= new MatTableDataSource(Response);
        this.dataSource.sort = this.sort;
       // this.dataSource=Response;
         });
    
    
    });
        
       
     




    

  }

  
getitemrecords(){
   return new Promise((resolve, reject) => {

    this.http.get(this.baseUrl+"blockchain/viewData/"+this.fromdate+'/'+this.category).subscribe((Response) =>{
       resolve(Response);
      },(error) => {
        reject(error);
      }
    );
    })
 }
}
