import { Component, OnInit } from '@angular/core';
import { DatePipe } from '../../../../node_modules/@angular/common';
import * as moment from 'moment';

import {Router} from "@angular/router";
import { TableDataService } from "./../../table-data.service"; 
@Component({
  selector: 'app-view-records',
  templateUrl: './view-records.component.html',
  styleUrls: ['./view-records.component.css'],
  providers:  [ TableDataService ]
})
export class ViewRecordsComponent implements OnInit {
d:any;
  fromDate:any;
  toDate:Date;
  selectedValue:any;
  datePipe = new DatePipe('en-US');
  constructor(private router: Router,public _dataService: TableDataService) { }

  ngOnInit() {
  }
    public onDate(event): void {
    this.d = event.value;
    this.fromDate=moment(this.d).format('DD-MM-YYYY');
    console.log("reported date is "+this.fromDate) ; 
   
  }
  changeDataSource(data)
  {
    this.selectedValue=data;
    console.log("selected value "+this.selectedValue);

  }
  submit()
  {
    
    this._dataService.setFromDate(this.fromDate);
    this._dataService.setCategory(this.selectedValue);

     this.router.navigate(['table']);

  }
}
